
.game{
    width: 100%;
    height: 100%;
}
.map{
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    &__mini{
        z-index: 100;
        position: absolute;
        background-color: grey;
        border: 2px solid black;
        box-shadow: 0 0 5px black;
        border-radius: 5px;
        top:10px;
        right:10px;
        width: 20%;
        height: 20%;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        &__full{
            position: absolute;
            width: 560px;
            height: 320px;
        }
        &__main{
            border-radius: 100%;
            width: 8px;
            height: 8px;
            position: relative;
            top:-8px;
            left:-16px;
            background-color: greenyellow;
            box-sizing: border-box;
            border: 1px solid black;
        }
        &__player{
            filter:blur(1px);
            border-radius: 100%;
            width: 8px;
            height: 8px;
            position: absolute;
            // top:-8px;
            // left:-16px;
            background-color: blue;
        }
    }
    &__full{
        // pointer-events: none;
        position: absolute;
        width: 3360px;
        height: 1920px;
        background-color: #ffffff;
        top:0;
        left:0;
        // transition: 0.000000000000000000000025s;
        
        &__player{
            position: absolute;
            top:0;
            left:0;
        }
        & audio{
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
            opacity: 0;
        }
        // &__tele{
        //     width: 48px;
        //     height: 48px;
        //     background-color: red;
        //     position: absolute;
        //     bottom:0;
        //     left:50%;
        //     transform: translateX(-50%);
        // }
    }
    &__object{
        width: 288px;
        height: 192px;
        position: absolute;
        top:863px;
        left:1535px
    }
    &__forge{
        position: absolute;
        width: 3360px;
        height: 1920px;
        background-color: #ffffff;
        top:0;
        left:0;
        z-index: 4;

    }
    
    &__view{
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__fake{
        // pointer-events: none;
        top:0;
        left:0;
        z-index: -1;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: aquamarine;
        & div{
            width: 100%;
            height: 100%;
            background-size: 48px 48px;
            background-repeat: repeat;
        }
    }
    
}

@keyframes bonHinh{
    0%{
        transform: translate(0,0) ;
        
    }
    100%{
        transform: translate(-192px,0) ;
    }
}
@keyframes bonHinhRotate{
    0%{
        transform: translate(0,0) rotate3d(0.5, 0, 0, 4.2334rad);
        
    }
    100%{
        transform: translate(-256px,0) rotate3d(0.5, 0, 0, 4.2334rad);
    }
}


